import { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import { FaChevronLeft, FaRegHeart } from "react-icons/fa";
import { BiShoppingBag } from "react-icons/bi";
import useCategories from "../../hooks/useCategories";
import chunkArray from "../../utils/chunkArray";
import { IoChevronDown } from "react-icons/io5";
import { useSidebar } from "../../contexts/sidebarContext";

const MenuHeader = ({ toggle }) => (
  <div className="MENU-HEADER flex items-center justify-between border-b border-secondary py-2 px-4">
    <div className="flex items-center gap-4 w-max">
      <button onClick={toggle}>
        <FaChevronLeft size={18} />
      </button>
      <h2 className="text-xl font-semibold">Categories</h2>
    </div>
    <div className="flex items-center gap-4">
      <Link to="/wishlist" className="text-primeRed">
        <FaRegHeart size={24} />
      </Link>
      <button className="text-primeRed">
        <BiShoppingBag size={24} />
      </button>
    </div>
  </div>
);

const Category = ({ category, onToggle, isSelected }) => (
  <motion.div
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    exit={{ opacity: 0 }}
    whileHover={{ scale: 1.05 }}
    onClick={() => onToggle(category._id)}
    className="relative rounded-lg break-all shadow-md cursor-pointer aspect-square p-1 pr-5 flex"
    style={{
      backgroundImage: `url(${category?.logoUrl})`,
      backgroundSize: "cover",
      backgroundPosition: "center",
    }}
  >
    {category?.subCategories?.length > 0 && (
      <span className="absolute top-1 right-1 z-[1]">
        <IoChevronDown
          color="white"
          className={`transition-transform duration-300 ease-in-out ${
            isSelected && "rotate-180"
          }`}
        />
      </span>
    )}
    {isSelected && category?.subCategories?.length > 0 && (
      <motion.span
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="absolute size-5 left-1/2 -translate-x-1/2 -bottom-7 rotate-45 bg-lightGray"
      />
    )}
  </motion.div>
);

const SubCategoryList = ({
  subCategories,
  selectedSubcategory,
  onSubcategoryToggle,
}) => (
  <motion.div
    initial={{ opacity: 0, height: 0 }}
    animate={{ opacity: 1, height: "auto" }}
    // exit={{ opacity: 0, height: 0 }}
    className="col-span-3 bg-lightGray px-4 divide-y-1 divide-secondary overflow-hidden"
  >
    {subCategories.map((sub) => (
      <Fragment key={sub._id}>
        <div
          className="relative flex items-center gap-4 py-1 cursor-pointer"
          onClick={() => onSubcategoryToggle(sub._id)}
        >
          <img
            src={sub.logoUrl}
            alt={sub._id}
            className="size-10 object-cover object-center"
          />

          {sub?.productTypes?.length > 0 ? (
            <p className="text-xs font-medium">{sub.name}</p>
          ) : (
            <Link to="" className="block flex-grow text-xs font-medium">
              {sub.name}
            </Link>
          )}

          {sub?.productTypes?.length > 0 && (
            <span className="absolute top-1/2 -translate-y-1/2 right-0 z-[1]">
              <IoChevronDown
                className={`transition-transform duration-300 ease-in-out ${
                  selectedSubcategory === sub._id && "rotate-180"
                }`}
              />
            </span>
          )}
        </div>
        <AnimatePresence>
          {selectedSubcategory === sub._id && (
            <ProductTypeList productTypes={sub.productTypes} />
          )}
        </AnimatePresence>
      </Fragment>
    ))}
  </motion.div>
);

const ProductTypeList = ({ productTypes }) => {
  const { closeSidebar } = useSidebar();

  return (
    <motion.div
      layout
      initial={{ opacity: 0, height: 0 }}
      animate={{ opacity: 1, height: "auto" }}
      exit={{ opacity: 0, height: 0 }}
      className="pl-4 bg-lightGray divide-y-1 divide-secondary overflow-hidden"
    >
      {productTypes.map((productType, index) => (
        <div key={index} className="py-2">
          <Link
            to={`/product-type/${productType._id}`}
            onClick={closeSidebar}
            className="block text-xs text-secondary hover:text-black font-medium"
          >
            {productType.name}
          </Link>
        </div>
      ))}
    </motion.div>
  );
};

const CategoryMenu = () => {
  const { categories } = useCategories();
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [selectedSubcategory, setSelectedSubcategory] = useState(null);
  const { closeSidebar } = useSidebar();

  const categorizedChunks = chunkArray(categories, 3);
  const handleToggle = (id) =>
    setSelectedCategory(id === selectedCategory ? null : id);
  const handleSubcategoryToggle = (id) =>
    setSelectedSubcategory(id === selectedSubcategory ? null : id);

  return (
    <div className="CATEGORY-MENU h-svh flex flex-col justify-between bg-white">
      <MenuHeader toggle={closeSidebar} />
      <div className="MENU-BODY h-full space-y-4 py-4 overflow-y-auto">
        {categorizedChunks.map((chunk, chunkIndex) => (
          <Fragment key={chunkIndex}>
            <div className="grid grid-cols-3 gap-x-4 px-4">
              {chunk.map((category) => (
                <Category
                  key={category._id}
                  category={category}
                  isSelected={selectedCategory === category._id}
                  onToggle={handleToggle}
                />
              ))}
            </div>
            <AnimatePresence>
              {chunk.map(
                (category) =>
                  selectedCategory === category._id && (
                    <SubCategoryList
                      key={category._id}
                      subCategories={category.subCategories}
                      selectedSubcategory={selectedSubcategory}
                      onSubcategoryToggle={handleSubcategoryToggle}
                    />
                  )
              )}
            </AnimatePresence>
          </Fragment>
        ))}
      </div>
    </div>
  );
};

export default CategoryMenu;
