import Logo from "../../components/Logo";
import SearchBar from "../../components/SearchBar";
import { navLinks } from "../../utils/Constants";
import NavBtn from "./NavBtn";
import Dropdown from "../../components/Dropdown";
import { useUser } from "../../contexts/userContext";
import { FaHeart, FaRegHeart, FaRegUser, FaUser } from "react-icons/fa";
import { BiShoppingBag, BiSolidShoppingBag } from "react-icons/bi";
import { useCart } from "../../contexts/cartContext";
import { useWishlist } from "../../contexts/wishlistContext";
import useMediaQuery from "../../hooks/useMediaQuery";
import { motion, useMotionValueEvent, useScroll } from "framer-motion";
import { useState } from "react";
import Hamburger from "../../components/Hamburger";
import CategoryMenu from "./CategoryMenu";
import { useSidebar } from "../../contexts/sidebarContext";
import { IoSearch } from "react-icons/io5";
import MobileSearch from "../../components/MobileSearch";

const PrimaryNav = () => {
  const { user } = useUser();
  const { cartItems } = useCart();
  const { wishlistItems } = useWishlist();
  const { openSidebar, isOpen } = useSidebar();
  const maxLg = useMediaQuery("(min-width: 1024px)");
  const maxMd = useMediaQuery("(min-width: 768px)");
  const { scrollY } = useScroll();
  const [scroll, setScroll] = useState(0);

  useMotionValueEvent(scrollY, "change", (latest) => {
    setScroll(latest);
  });

  return (
    <header
      className={`w-full z-20 ${
        scroll > 0
          ? "sticky bg-white top-0 shadow-drop"
          : "border-b border-secondary"
      }`}
    >
      <motion.nav
        initial="closed"
        animate={isOpen ? "open" : "closed"}
        className="container relative max-w-full flex items-center justify-between"
      >
        <div className="flex items-center gap-4">
          {!maxLg && <Hamburger toggle={() => openSidebar(<CategoryMenu />)} />}
          <a
            href="/"
            className="LOGO-SECTION w-60 max-lg:w-50 max-md:w-40 max-sm:w-30 relative bottom-[2px] flex-shrink-0"
          >
            <Logo />
          </a>
        </div>

        {maxMd && (
          <ul className="flex gap-8 max-xl:gap-4">
            {navLinks.map((link) => (
              <li
                key={link.title}
                className="h-16 font-medium text-lg max-xl:text-base max-md:text-sm"
              >
                <Dropdown
                  url={link.url}
                  content={link.content}
                  animation="highlight"
                >
                  {link.title}
                </Dropdown>
              </li>
            ))}
          </ul>
        )}

        <div className="BUTTONS-SECTION flex justify-center items-center gap-8 max-xl:gap-6 max-sm:gap-4">
          {maxLg ? (
            <div className="SEARCH-SECTION">
              <SearchBar />
            </div>
          ) : (
            <button type="button" onClick={() => openSidebar(<MobileSearch />)}>
              <IoSearch className="text-primary text-2xl" />
            </button>
          )}

          <div className="PROFILE-BUTTONS flex items-center gap-6 max-sm:gap-4">
            <NavBtn
              id={1}
              logo={<FaRegUser />}
              fillLogo={<FaUser />}
              url="/account"
            />
            {user && (
              <NavBtn
                id={2}
                logo={<FaRegHeart />}
                fillLogo={<FaHeart />}
                url="/wishlist"
                count={wishlistItems.length}
              />
            )}
            <NavBtn
              id={3}
              logo={<BiShoppingBag />}
              fillLogo={<BiSolidShoppingBag />}
              count={cartItems.length}
            />
          </div>
        </div>
      </motion.nav>
    </header>
  );
};

export default PrimaryNav;
