import { FaChevronLeft } from "react-icons/fa6";
import { useSidebar } from "../contexts/sidebarContext";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useSearchHistory } from "../contexts/searchHistoryContext";
import { useSearchSuggestions } from "../services/searchServices";
import useDebounce from "../hooks/useDebounce";
import useForm from "../hooks/useForm";
import { IoSearch, IoTrendingUp } from "react-icons/io5";
import { MdHistory } from "react-icons/md";
import { IoIosSearch } from "react-icons/io";
import { TRENDING_SEARCHES } from "../utils/Constants";

const MobileSearch = () => {
  const navigate = useNavigate();
  const [isFocused, setIsFocused] = useState(false);
  const { previousSearches, addSearch } = useSearchHistory();
  const { closeSidebar } = useSidebar();

  const onSubmitHandler = (formData) => {
    const { query } = formData;

    if (!query || query?.trim === "") return;

    navigate(`/search?query=${query}`);
    addSearch(query);
    clearForm();
    setIsFocused(false);
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  const debouncedQuery = useDebounce(formData.query);
  const shouldFetchSuggestions =
    isFocused && debouncedQuery && debouncedQuery.length > 0;

  const {
    data: suggestions,
    isLoading,
    isError,
  } = useSearchSuggestions(shouldFetchSuggestions ? debouncedQuery : null);

  const handleSuggestionClick = (suggestion) => {
    navigate(`/search?query=${suggestion}`);
    addSearch(suggestion);
    clearForm();
    setIsFocused(false);
  };

  return (
    <div className="bg-white h-svh relative">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p className="text-lg font-medium">Search</p>
      </div>

      <div className="BODY h-full flex flex-col">
        <form
          onSubmit={handleSubmit}
          className="h-14 w-full px-4 py-2 relative flex-shrink-0"
        >
          <input
            type="search"
            name="query"
            value={formData.query || ""}
            onChange={handleChange}
            onFocus={() => setIsFocused(true)}
            onBlur={() => setTimeout(() => setIsFocused(false), 200)}
            placeholder="Search"
            className="size-full rounded-full border border-primary outline-none pl-12 pr-4 placeholder:text-sm placeholder:text-grey placeholder:font-secondary"
          />

          <button
            type="submit"
            className="absolute left-8 top-1/2 -translate-y-1/2"
          >
            <IoSearch className="text-primary text-2xl" />
          </button>
        </form>

        <ul className="flex-grow overflow-y-auto pb-4">
          {!debouncedQuery || debouncedQuery?.length === 0 ? (
            previousSearches.length > 0 ? (
              previousSearches.map((item, index) => (
                <li
                  key={index}
                  onClick={() => handleSuggestionClick(item)}
                  className="flex items-center gap-2 cursor-pointer text-xs px-4 py-2 hover:bg-lightGray"
                >
                  <MdHistory size={16} className="flex-shrink-0" />
                  <p className="capitalize truncate">{item}</p>
                </li>
              ))
            ) : (
              <div className="p-2">
                <p className="text-sm text-primary text-center">
                  No recent searches
                </p>
              </div>
            )
          ) : debouncedQuery?.length > 0 ? (
            isLoading ? (
              <div className="p-2">
                <p className="text-sm text-primary text-center">
                  No recent searches
                </p>
              </div>
            ) : isError ? (
              <div className="py-2">
                <p className="text-sm text-primary text-center">
                  No matches found
                </p>
              </div>
            ) : (
              suggestions?.map((suggestion, index) => (
                <li
                  key={index}
                  onClick={() => handleSuggestionClick(suggestion)}
                  className="flex items-center gap-2 cursor-pointer text-xs px-4 py-2 hover:bg-lightGray"
                >
                  <IoIosSearch size={16} className="flex-shrink-0" />
                  <p className="capitalize truncate">{suggestion}</p>
                </li>
              ))
            )
          ) : null}

          {TRENDING_SEARCHES.length > 0 && (
            <>
              <div className="py-2 px-4 bg-darkGray">
                <p className="text-sm text-white leading-none">
                  Trending searches
                </p>
              </div>

              {TRENDING_SEARCHES.map((value) => (
                <li
                  key={value}
                  onClick={() => handleSuggestionClick(value)}
                  className="flex items-center gap-2 cursor-pointer text-xs px-4 py-2 hover:bg-lightGray"
                >
                  <IoTrendingUp size={16} className="flex-shrink-0" />
                  <p className="capitalize truncate">{value}</p>
                </li>
              ))}
            </>
          )}
        </ul>
      </div>
    </div>
  );
};

export default MobileSearch;
