import { useEffect, useState } from "react";
import { useSidebar } from "../../contexts/sidebarContext";
import { AnimatePresence } from "framer-motion";
import Cart from "../Cart";
import { Link, useLocation } from "react-router-dom";
import AccountDrop from "../../components/AccountDrop";

const NavBtn = ({ logo, fillLogo, url, id, count }) => {
  const location = useLocation();
  const [isSelected, setIsSelected] = useState(false);
  const [isLocation, setIsLocation] = useState(false);
  const { openSidebar } = useSidebar();
  const showDropdown = isSelected && !isLocation && id === 1;

  useEffect(() => {
    if (location.pathname.includes(url)) setIsLocation(true);
    else setIsLocation(false);
  }, [url, location.pathname]);

  const handleMouseEnter = () => {
    if (isSelected) return;
    setIsSelected(true);
  };

  const handleMouseLeave = () => {
    if (location.pathname.includes(url)) return;
    setIsSelected(false);
  };

  const handleClick = () => {
    if (id === 3) openSidebar(<Cart />);
  };

  return (
    <div
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onBlur={() => setIsSelected(false)}
      onClick={handleClick}
      className="relative h-16 max-sm:h-14 flex justify-center items-center flex-shrink-0"
    >
      <Link
        to={url}
        className={`block ${
          isSelected || isLocation ? "text-primeRed" : "text-primary"
        }`}
      >
        <div className="BTN-LOGO-SECTION relative">
          <span className="text-2xl max-sm:text-xl">
            {isSelected || isLocation ? fillLogo : logo}
          </span>

          {count && count !== 0 ? (
            <span className="bg-primeRed flex items-center justify-center size-4 rounded-full absolute -top-2 -right-2">
              <p className="text-white text-[8px] leading-none">{count}</p>
            </span>
          ) : (
            <></>
          )}
        </div>

        <span
          style={{
            transform: isSelected || isLocation ? "scaleX(1)" : "scaleX(0)",
          }}
          className="absolute bottom-0 -left-3 -right-3 h-1 origin-center scale-x-0 rounded-full bg-primeRed transition-transform duration-300 ease-out"
        />
      </Link>

      <AnimatePresence>{showDropdown && <AccountDrop />}</AnimatePresence>
    </div>
  );
};

export default NavBtn;
